import { useState } from "react";
import "./App.css";
import { Selector, Space, Button } from "antd-mobile";

function App() {
  const originDatas = [
    {
      id: "a1",
      name: "1号",
      dianpao: 0,
      dianhu: 0,
      zimo: 0,
      angang: 0,
      minggang: 0,
      score: 0,
    },
    {
      id: "a2",
      name: "2号",
      dianpao: 0,
      dianhu: 0,
      zimo: 0,
      angang: 0,
      minggang: 0,
      score: 0,
    },
    {
      id: "a3",
      name: "3号",
      dianpao: 0,
      dianhu: 0,
      zimo: 0,
      angang: 0,
      minggang: 0,
      score: 0,
    },
    {
      id: "a4",
      name: "4号",
      dianpao: 0,
      dianhu: 0,
      zimo: 0,
      angang: 0,
      minggang: 0,
      score: 0,
    },
  ];
  const [datas, setDatas] = useState(originDatas);
  const options = [
    {
      label: "1号",
      value: "a1",
    },
    {
      label: "2号",
      value: "a2",
    },
    {
      label: "3号",
      value: "a3",
    },
    {
      label: "4号",
      value: "a4",
    },
  ];
  const [menuType, setMenuType] = useState(0);
  const [oneName, setOneName] = useState("自摸");
  const [twoNameFirst, setTwoNameFirst] = useState("谁点跑");
  const [twoNameSecond, setTwoNameSecond] = useState("谁胡了");
  const [scoringType, setScoringType] = useState("zimo");
  const [values, setValues] = useState({
    oneValue: "a1",
    twoFirstValue: "a1",
    twoSecondValue: "a1",
  });

  const handleClick = (type: any) => {
    console.log(type, menuType);
    setScoringType(type);
    switch (type) {
      case "dianpao":
        setTwoNameFirst("谁点炮");
        setTwoNameSecond("谁胡了");
        setMenuType(1);
        break;
      case "zimo":
        setOneName("谁自摸");
        setMenuType(0);
        break;
      case "angang":
        setOneName("谁暗杆");
        setMenuType(0);
        break;
      case "minggang":
        setTwoNameFirst("谁给杠");
        setTwoNameSecond("谁杠了");
        setMenuType(1);
        break;
      default:
        break;
    }
  };

  const copy = (arr: any) => {
    return JSON.parse(JSON.stringify(arr));
  };

  const handleConfirm = () => {
    console.log(scoringType, values);
    const id = values.oneValue;
    const id1 = values.twoFirstValue;
    const id2 = values.twoSecondValue;
    console.log(id)
    switch (scoringType) {
      case "dianpao":
        let index01;
        let index02;
        let newDatas = copy(datas);
        const newData01 = datas.find((item:any, i:any) => {
          index01 = i;
          return item.id === id1;
        });
        const newData02 = datas.find((item:any, i:any) => {
          index02 = i;
          return item.id === id2;
        });
        // @ts-ignore
        newData01.score -= 1;
        // @ts-ignore
        newData02.score += 1;
        // @ts-ignore
        newData01.dianpao += 1;
        // @ts-ignore
        newData02.dianhu += 1;
        newDatas.splice(index01,1,newData01)
        newDatas.splice(index02,1,newData02)
        setDatas(newDatas)
        break;
      case "zimo":
        let index1;
        let newDatas1 = copy(datas);
        const newData1 = datas.find((item:any, i:any) => {
          index1 = i;
          return item.id === id;
        });
        newDatas1.forEach((item: any) => {
          item.score -= 2;
        });
        // @ts-ignore
        newData1.zimo += 1;
        // @ts-ignore
        newData1.score += 6;
        newDatas1.splice(index1,1,newData1)
        setDatas(newDatas1)

        break;
      case "angang":
        let index2;
        let newDatas2 = copy(datas);
        const newData2 = datas.find((item:any, i:any) => {
          index2 = i;
          return item.id === id;
        });
        newDatas2.forEach((item: any) => {
          item.score -= 1;
        });
        // @ts-ignore
        newData2.angang += 1;
        // @ts-ignore
        newData2.score += 3;
        newDatas2.splice(index2,1,newData2)
        setDatas(newDatas2)
        break;
      case "minggang":
        let index31;
        let index32;
        let newDatas3 = copy(datas);
        const newData31 = datas.find((item:any, i:any) => {
          index31 = i;
          return item.id === id1;
        });
        const newData32 = datas.find((item:any, i:any) => {
          index32 = i;
          return item.id === id2;
        });
        // @ts-ignore
        newData31.score -= 1;
        // @ts-ignore
        newData32.score += 1;
        // @ts-ignore
        newData32.minggang += 1;
        newDatas3.splice(index31,1,newData31)
        newDatas3.splice(index32,1,newData32)
        setDatas(newDatas3)
        break;
      default:
        break;
    }
  };

  return (
    <div className="app">
      <div className="app__fraction">
        <div className="fraction__item">
          <div className="fraction__item_main">姓名</div>
          <div className="fraction__item_main">点炮</div>
          <div className="fraction__item_main">点胡</div>
          <div className="fraction__item_main">自摸</div>
          <div className="fraction__item_main">暗杠</div>
          <div className="fraction__item_main">明杠</div>
          <div className="fraction__item_main">分数</div>
        </div>
        {datas.map((item) => (
          <div className="fraction__item">
            <div className="fraction__item_main primaty">{item.name}</div>
            <div className="fraction__item_main">{item.dianpao}</div>
            <div className="fraction__item_main">{item.dianhu}</div>
            <div className="fraction__item_main">{item.zimo}</div>
            <div className="fraction__item_main">{item.angang}</div>
            <div className="fraction__item_main">{item.minggang}</div>
            <div className="fraction__item_main">{item.score}</div>
          </div>
        ))}
      </div>
      <div className="app__menu">
        <div className="app__menu_btn">
          <Button
            color="primary"
            fill="solid"
            onClick={() => handleClick("dianpao")}
          >
            点炮
          </Button>
          <Button
            color="success"
            fill="solid"
            onClick={() => handleClick("zimo")}
          >
            自摸
          </Button>
          <Button
            color="danger"
            fill="solid"
            onClick={() => handleClick("angang")}
          >
            暗杠
          </Button>
          <Button
            color="warning"
            fill="solid"
            onClick={() => handleClick("minggang")}
          >
            明杠
          </Button>
        </div>
        <div className="app__menu_operation">
          {menuType ? (
            <>
              <div className="opration__item">
                <div className="item_title">{twoNameFirst}</div>
                <div className="item__selectors">
                  <Selector
                    options={options}
                    defaultValue={[values.twoFirstValue]}
                    onChange={(arr) => {
                      setValues({
                        ...values,
                        twoFirstValue: arr[0],
                      });
                    }}
                  />
                </div>
              </div>
              <div className="opration__item">
                <div className="item_title">{twoNameSecond}</div>
                <div className="item__selectors">
                  <Selector
                    options={options}
                    defaultValue={[values.twoSecondValue]}
                    onChange={(arr) => {
                      setValues({
                        ...values,
                        twoSecondValue: arr[0],
                      });
                    }}
                  />
                </div>
              </div>
            </>
          ) : (
            <div className="opration__item">
              <div className="item_title">{oneName}</div>
              <div className="item__selectors">
                <Selector
                  options={options}
                  defaultValue={[values.oneValue]}
                  onChange={(arr) => {
                    console.log(arr)
                    setValues({
                      ...values,
                      oneValue: arr[0],
                    });
                  }}
                />
              </div>
            </div>
          )}
        </div>
        <div className="app__menu_submit">
          <Button color="success" fill="solid" onClick={handleConfirm}>
            确定
          </Button>
        </div>
      </div>
    </div>
  );
}

export default App;
